/* ########################     navbar  ###################### */
.navbar {
  width: 230px;
  height: 100vh;
  background: #fff;
  display: flex;
  justify-content: center;
  box-shadow: 8px 0px 15px 0px #e7e7e7;
  position: fixed; }
  .navbar__logo {
    font-size: 35px;
    line-height: 28px;
    font-weight: 700;
    color: #000;
    display: block;
    text-align: center;
    margin-bottom: 30px; }
    .navbar__logo .point {
      font-size: 60px;
      line-height: 28px;
      color: #3DD0A4; }
  .navbar__item {
    margin: 10px 0; }
  .navbar__link {
    width: 230px;
    display: inline-flex;
    align-items: center;
    font-size: 20px;
    color: #000;
    position: relative;
    padding: 10px 22px; }
    .navbar__link:hover {
      background: #F8F8F8;
      font-weight: 500; }
    .navbar__link::after {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      width: 5px;
      height: 0;
      opacity: 0;
      background: #3DD0A4;
      z-index: 1;
      transition: all 0.2s; }
    .navbar__link:hover::after {
      height: 100%;
      opacity: 1;
      top: 0; }
    .navbar__link .fa {
      font-size: 28px;
      color: #3DD0A4;
      margin-right: 10px; }

/* ########################     navbar END  ###################### */
