/* ########################     section addProduct ###################### */
.form {
  width: 650px;
  background: #fff;
  box-shadow: 0px 0px 10px 0px #e7e7e7;
  margin-bottom: 25px;
  padding: 15px 25px; }
  .form__row {
    display: flex;
    justify-content: space-between; }
  .form__col1, .form__col2 {
    display: flex; }
  .form__col2 {
    height: 224px; }
  .form__text {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 16px;
    text-align: right;
    display: inline-flex;
    justify-content: space-between;
    flex-direction: column;
    margin: 20px 0px; }
  .form__input {
    width: 210px; }
    .form__input input,
    .form__input .select {
      width: 200px;
      height: 36px;
      outline: none;
      border: none;
      border-radius: none;
      background: #F8F8F8;
      font-size: 16px;
      font-weight: 400;
      margin: 10px 0px 10px 10px;
      padding-left: 10px; }
    .form__input .select {
      cursor: pointer; }
      .form__input .select option {
        border: none; }
    .form__input [type="date"] {
      cursor: text; }
  .form .btn {
    cursor: pointer;
    width: 120px;
    display: block;
    background: #3DD0A4;
    font-size: 14px;
    color: white;
    outline: none;
    border-width: 0;
    text-transform: uppercase;
    padding: 10px 0;
    margin: 10px 0 0 auto; }
    .form .btn:hover {
      background: #60e6c5; }

.addProduct {
  margin-top: 25px; }
  .addProduct .table {
    font-family: 'Roboto';
    text-align: left;
    background: #fff;
    color: #000;
    box-shadow: 0px 0px 10px 0px #e7e7e7;
    width: 1140px; }
    .addProduct .table thead {
      border-bottom: 3px solid #F8F8F8; }
      .addProduct .table thead th {
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        padding: 14px 26px; }
    .addProduct .table tbody .tdProductName {
      font-weight: 500; }
    .addProduct .table tbody td {
      font-size: 18px;
      border-bottom: 2px solid #F8F8F8;
      padding: 16px 26px; }
    .addProduct .table tbody .red {
      color: red; }
    .addProduct .table tbody .green {
      color: #3DD0A4; }
    .addProduct .table tr:hover td {
      background: rgba(61, 208, 164, 0.1); }

/* ########################     section addProduct END ###################### */
